<script>
import Layout from "@/components/Layout";
import Alert from "@/components/Alert";
export default {
  components: {
    Layout,
    Alert,
  },
  data() {
    return {
      navInfoList: [
        "关于美团",
        "加入我们",
        "商家入驻",
        "帮助中心",
        "美团手机版",
      ],
      userName: "",
      password: "",
      warnInfo: {
        status: "success",
      },
    };
  },
  methods: {
    async handleSubmit() {
      // 因为考虑到登录的账号需要与首页进行联动 且是跨组件 使用vuex ,也可以用eventBus
      // this.$api.postLogin({userName : this.userName, password : this.password});

      this.warnInfo = await this.$store.dispatch("userStore/asyPostLogin", {
        userName: this.userName,
        password: this.password,
      });
      if(this.warnInfo.status === 'success') {
        this.$router.push({
          name : 'Home'
        })
      }
    },
  },
};
</script>

