<template>
  <div
    class="alert-wrapper"
    :style="{
      visibility: warnInfo.status === 'success' ? 'hidden' : 'visible',
    }"
  >
    <!-- visibility:占位不可触发事件 -->
    <i :class="[type]"></i>
    <span>{{ warnInfo.msg }}</span>
  </div>
</template>

<script>
export default {
  props: {
    warnInfo: Object,
    type: {
      type: String,
      default: "alter-type-warn ",
    },
  },
};
</script>

<style lang="less" scoped>
.alert-wrapper {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #f5d8a7;
  border-radius: 2px;
  background: #fff6db;
  font-size: 12px;
  min-height: 18px;
  box-sizing: content-box;
  i {
    display: inline-block;
    width: 17px;
    height: 17px;
    margin-right: 5px;
    &.alter-type-warn {
      background: url(//s0.meituan.net/bs/file/?f=fe-sso-fs:build/assets/icons.c1b3784.png)
        no-repeat;
      background-position: 0 -296px;
